<style scoped>
img {width: 2.5rem;height: 2.5rem;border-radius: 50%;margin-right: 0.5rem;}
#app .el-table .cell{white-space: nowrap!important;}
</style>>
<template>
<div class="bg">
  <div class="content" v-if="!editShow && !userList">
     <Search doctor="true"></Search>
      <el-button type="primary" size="mini" @click="hEdit(0)">+添加医生</el-button>
    <el-table
      :data="tableData"
       style="width: 100%;margin-top:0.5rem" :cell-style="{fontSize:'0.65rem'}" :header-cell-style="{background:'#eef1f6',color:'#606266',fontSize:'0.6rem'}">
       
      <el-table-column  align="center" 
        label="ID"
      >
        <template slot-scope="scope">
          <span >22</span>
        </template>
      </el-table-column>
       <el-table-column  align="center" 
        label="科室"
      >
        <template slot-scope="scope">
          <span >一级分类</span>
        </template>
      </el-table-column>
         <el-table-column  align="center" 
        label="名字"
      >
        <template slot-scope="scope">
      
           <!-- <el-popover trigger="hover" placement="top">
          <p class="mb5 font12">评价等级:优秀</p>
          <p class="mb5 font12">咨询用户: XXX</p>
          <p class="mb5 font12">咨询J价格: XXX</p>
          <p class="mb5 font12">余额: XXX</p> -->
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        <!-- </el-popover> -->
        </template>
      </el-table-column>
        <el-table-column  align="center" 
        label="电话"
      >
        <template slot-scope="scope">
          <span >1876736377</span>
        </template>
      </el-table-column>
        <el-table-column  align="center" 
        label="所属医院"
      >
        <template slot-scope="scope">
          <span >浙一医院</span>
        </template>
      </el-table-column>
         <el-table-column  align="center" 
        label="创建时间"
      >
        <template slot-scope="scope">
          <span >2019.09.21</span>
        </template>
      </el-table-column>
         <el-table-column  align="center" 
        label="状态"
      >
        <template slot-scope="scope">
          <span class="blue">接诊中</span>
        </template>
      </el-table-column>
      <el-table-column  align="center"  label="操作" fixed="right"  width="280">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="hEdit(1)">编辑</el-button>
            <el-button size="mini"
            @click="detail">咨询用户</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="p20 textRight">
      <el-pagination  
        @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage1"
            :page-size="100"
            layout="total,prev, pager, next, jumper"
            :total="1000">
        </el-pagination>
    </div>
  </div>
  <!-- 编辑用户信息 -->
  <div class="content p20" v-if="editShow">
    <div class="blue mb20 back " @click="back">
      <i class="iconfont icon-cs-fh-1 "></i><a class="font18 pl20">返回</a>
    </div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm p20">
     
      <el-form-item label="ID" prop="name">
       <span class="blue">56</span>
      </el-form-item>
      <el-form-item label="所属科室" >
           <el-select v-model="value" placeholder="请选择">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
      </el-form-item>
      <el-form-item label="医生名称" >
        <el-input v-model="ruleForm.nickname"></el-input>
      </el-form-item> 
       <el-form-item label="电弧" >
        <el-input v-model="ruleForm.nickname"></el-input>
      </el-form-item> 
       <el-form-item label="所属医院" >
        <el-input v-model="ruleForm.nickname"></el-input>
      </el-form-item>  
       <el-form-item label="评价等级" >
        <el-input v-model="ruleForm.nickname"></el-input>
      </el-form-item>    
       <el-form-item label="咨询价格" >
        <el-input v-model="ruleForm.nickname"></el-input>
      </el-form-item> 
      
       <el-form-item label="状态" >
         <el-radio-group v-model="ruleForm.resource">
          <el-radio label="接诊中"></el-radio>
           <el-radio label="休息中"></el-radio>
          
        </el-radio-group>
      </el-form-item> 
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="back">返回列表</el-button>
      </el-form-item>
    </el-form>
  </div>
  <!-- 查看咨询用户 -->
  <div class="content" v-if="userList">
     <div class="blue mb20 back " @click="back">
      <i class="iconfont icon-cs-fh-1 "></i><a class="font18 pl20">返回</a>
    </div>
      <el-table
      :data="tableData"
        style="width: 100%;margin-top:0.5rem" :cell-style="{fontSize:'0.65rem'}" :header-cell-style="{background:'#eef1f6',color:'#606266',fontSize:'0.6rem'}">
      <el-table-column  align="center" 
        label="咨询时间"
      >
        <template slot-scope="scope">
          <span >{{ scope.row.date }}</span>
        </template>
      </el-table-column>
    
      <el-table-column  align="center" 
        label="用户Id"
      >
        <template slot-scope="scope">
          <span >22</span>
        </template>
      </el-table-column>
        <el-table-column  align="center" 
        label="用户昵称"
      >
        <template slot-scope="scope">
          <span >XXX</span>
        </template>
      </el-table-column>
        <el-table-column  align="center" 
        label="余额"
      >
        <template slot-scope="scope">
          <span >22</span>
        </template>
      </el-table-column>
       <el-table-column  align="center" 
        label="咨询状态"
        width="300"
      >
        <template slot-scope="scope">
           <el-radio-group v-model="ruleForm.resource">
          <el-radio label="接诊中"></el-radio>
          <el-radio label="待接诊"></el-radio>
           <el-radio label="已完成"></el-radio>
          
        </el-radio-group>
        </template>
      </el-table-column>
         <el-table-column  align="center" 
        label="咨询金额"
      >
        <template slot-scope="scope">
          <span >89</span>
        </template>
      </el-table-column>
      <!-- <el-table-column  align="center"  label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="hEdit(1)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index)">删除</el-button>
        </template>
      </el-table-column> -->
    </el-table>
  </div>
</div>
</template>

<script>
import Search from '@/components/search'
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          num1:1,
          num2:1
          
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄',
           num1:1,
          num2:1
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄',
           num1:1,
          num2:1
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
           num1:1,
          num2:1
        }],
          currentPage1: 5,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        editShow:false,
        userList:false,
        ruleForm: {
          name: '',
          nickname: '',
          tel: '',
          resource: '',
         
        },
        rules: {
          name: [
            { required: true, message: '请输入真实姓名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          tel: [
            { required: true, message: '请输入电话号码', trigger: 'change' }
          ],
          nickname: [
            { required: true, message: '请输入微信号码', trigger: 'change' }
          ],
        },
         options: [{
          value: '选项1',
          label: '分类1'
          }, {
          value: '选项2',
          label: '分类2'
          }, {
          value: '选项3',
          label: '分类3'
          }, {
          value: '选项4',
          label: '分类4'
          }],
          value: ''
      }
    },
    methods: {
      hEdit(index) {
        console.log(index);
        var that=this;
        that.editShow=true;
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      back() {
       var that=this;
        that.editShow=false;
        this.userList=false;
      },
      detail(){
          this.userList=!this.userList;
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
      
    },
       //注册组件
    components: {
      Search,
     
    },
  }
</script>


